import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getCurrentTuitionReceipts = ({ studentId, yearEduId, semesterId, period }) =>
  request({
    prefix,
    url: '/CreditReceivable/BrowseByStudent',
    method: 'GET',
    params: { studentId, yearEduId, semesterId, period },
    data: {},
  });

export const getStudents = ({
  trainTypeId,
  studyFieldId,
  studentCode,
  fullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix: '/api/students',
    url: '/Student/GetStudentForCreditReceivable',
    method: 'GET',
    params: {
      trainTypeId,
      studyFieldId,
      studentCode,
      fullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getReceiptConfig = () =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: { configTypeId: 1 },
    data: {},
  });

export const saveReceipt = async res =>
  request({
    prefix,
    url: '/Receipt/CreateReceiptDetail',
    method: 'POST',
    params: {},
    data: { ...res },
  });

export const exportReceipt = async (id, name) =>
  requestDownload({
    defaultFileName: `${name}.pdf`,
    prefix,
    url: '/Receipt/ExportReportTuitionCredit',
    data: { receiptId: id },
    method: 'POST',
  });



