import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Modal, Checkbox, Table, message } from 'antd';
import { TableData } from 'components/common';
import { Btn } from 'components/common/button';
import {
  getStudents,
  getCurrentTuitionReceipts,
  saveReceipt,
  exportReceipt,
} from 'services/currentTuitionReceipts';
import { moneyToString } from 'utils/formatUtil';
import { getUserInfo } from 'utils/userUtils';
import moment from 'moment';
import SearchBarStudent from './SearchBarStudent';
import SearchBar from './SearchBar';

class CurrentTuitionReceipts extends Component {
  state = {
    selectedStudent: {},
    visibleModal: false,
    loadingSaveReceipt: false,
    paramSearchStudent: {},
    receiptSelecteds: [],
    selectedRowKeys: [],
    tuitionReceipts: [],
    isPrintAfterSave: false,
  };

  setVisibleModal = visible => {
    this.setState({
      visibleModal: visible,
    });
  };

  getCurrentTuitionReceipts = async selectedStudent => {
    const res = await getCurrentTuitionReceipts(selectedStudent);
    const tuitionReceipts = this.addIndex(res.data.items);
    this.setState({ tuitionReceipts });
  };

  addIndex = tuitionReceipts => {
    let index = 0;
    tuitionReceipts.forEach(tuitionReceipt => {
      index += 1;
      tuitionReceipt.index = index;
    });
    return tuitionReceipts;
  };

  columns = [
    {
      title: 'STT',
      dataIndex: 'index',
      key: 'index',
    },
    {
      title: 'Khoản nộp',
      dataIndex: 'paymentText',
      key: 'paymentText',
    },
    {
      title: 'Dư đầu kì',
      dataIndex: 'unpaidAmount',
      render: text => (text ? moneyToString(text) : 0),
      key: 'unpaidAmount',
    },
    {
      title: 'Phải nộp kỳ',
      dataIndex: 'payableAmount',
      render: text => (text ? moneyToString(text) : 0),
      key: 'payableAmount',
    },
    {
      title: 'Đã nộp kỳ',
      dataIndex: 'paidAmount',
      render: text => (text ? moneyToString(text) : 0),
      key: 'paidAmount',
    },
    {
      title: 'Số tiền',
      dataIndex: 'paymentAmount',
      key: 'paymentAmount',
      render: text => (text ? moneyToString(text) : 0),
    },
  ];

  columnsStudentSearch = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <Btn onClick={() => this.pickStudent(record)}>Lựa chọn SV</Btn>
        </Fragment>
      ),
    },
  ];

  handleOk = () => {
    this.setState({
      visibleModal: false,
    });
  };

  handleCancel = () => {
    this.setState({
      visibleModal: false,
    });
  };

  handleParamSearchStudent = values => {
    this.setState({ paramSearchStudent: values });
  };

  pickStudent = value => {
    const { yearEduId, semesterId, period } = this.formRef.props.form.getFieldsValue();
    const selectedStudent = { ...value, yearEduId, semesterId, period };
    this.setState({
      visibleModal: false,
      selectedStudent,
    });
    this.getCurrentTuitionReceipts(selectedStudent);
  };

  handleAddOther = (receivableId, receivableLabel, amount) => {
    const { tuitionReceipts } = this.state;

    tuitionReceipts.push({
      receivableId,
      paymentText: receivableLabel,
      payableAmount: amount,
      paymentAmount: amount,
      type: 'other',
      index: tuitionReceipts.length + 1,
    });
    this.setState({ tuitionReceipts });
  };

  handleRemoveReceipt = () => {
    const { tuitionReceipts, receiptSelecteds } = this.state;
    const removeIndexs = receiptSelecteds.map(receiptSelected => receiptSelected.index);
    const newTuitionReceipts = tuitionReceipts.filter(
      tuitionReceipt => !removeIndexs.includes(tuitionReceipt.index)
    );
    this.setState({
      tuitionReceipts: this.addIndex(newTuitionReceipts),
      receiptSelecteds: [],
      selectedRowKeys: [],
    });
  };

  saveReceipts = () => {
    this.formRef.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }

      this.setState({ loadingSaveReceipt: true });

      const { receiptSelecteds, selectedStudent } = this.state;

      const creditReceiptDetails = receiptSelecteds
        .filter(receiptSelected => receiptSelected.type !== 'other')
        .map(receiptSelected => {
          const {
            creditReceivableId,
            unpaidAmount,
            payableAmount,
            paidAmount,
            reducedPercentage,
            reducedAmount,
          } = receiptSelected;
          const result = {
            creditReceivableId,
            unpaidAmount,
            payableAmount,
            paidAmount,
            reducedPercentage,
            reducedAmount,
            isSelected: true,
          };
          return result;
        });

      const otherReceiptDetails = receiptSelecteds
        .filter(receiptSelected => receiptSelected.type === 'other')
        .map(receiptSelected => {
          const result = {
            receivableId: receiptSelected.receivableId,
            unpaidAmount: 0,
            payableAmount: receiptSelected.paymentAmount,
            paidAmount: 0,
            reducedPercentage: 0,
            reducedAmount: 0,
            isSelected: true,
          };
          return result;
        });

      const { studentId } = selectedStudent;

      const {
        yearEduId,
        semesterId,
        paymentTypeId,
        paymentTime,
        paymentDate,
        paymentContent,
        receiptConfigId,
      } = values;

      const res = {
        id: undefined,
        yearEduId,
        semesterId,
        paymentTypeId,
        paymentTime,
        paymentDate: moment(paymentDate).format('YYYY/MM/DD'),
        paymentContent,
        casher: getUserInfo().id,
        currency: 'VNĐ',
        receiptConfigId,
        studentId,
        candidateId: 0,
        amount: 0,
        remainAmt: 0,
        reducedAmt: 0,
        creditReceiptDetails,
        otherReceiptDetails,
      };

      try {
        const resSave = await saveReceipt(res);
        await this.printReceipt(resSave.data.receiptId);
        this.resetDataToInit();
        message.success('Tạo hóa đơn thành công');
      } catch (error) {
        message.error('Tạo hóa đơn không thành công');
      }

      this.setState({ loadingSaveReceipt: false });
    });
  };

  printReceipt = async id => {
    const { isPrintAfterSave, selectedStudent } = this.state;
    if (isPrintAfterSave) {
      const { fullname, className } = selectedStudent;
      let filename = 'Thu tiền học phí';
      if (fullname && fullname !== '') filename = filename.concat(' - ').concat(fullname);
      if (className && className !== '') filename = filename.concat(' - ').concat(className);
      await exportReceipt(id, filename);
    }
  };

  togglePrintAfterSave = () => {
    this.setState({ isPrintAfterSave: !this.state.isPrintAfterSave });
  };

  resetDataToInit = () => {
    this.formRef.props.form.resetFields(['studentCode', 'receivable', 'amount']);
    this.setState({
      selectedStudent: {},
      visibleModal: false,
      loadingSaveReceipt: false,
      paramSearchStudent: {},
      receiptSelecteds: [],
      selectedRowKeys: [],
      tuitionReceipts: [],
      isPrintAfterSave: false,
    });
  };

  render() {
    const {
      paramSearchStudent,
      selectedStudent,
      receiptSelecteds,
      tuitionReceipts,
      loadingSaveReceipt,
    } = this.state;

    const rowSelection = {
      selectedRowKeys: this.state.selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ selectedRowKeys, receiptSelecteds: selectedRows });
      },
      getCheckboxProps: record => ({
        disabled: record.paymentAmount === 0,
      }),
    };

    return (
      <Card>
        <Row>
          <Col span={24}>
            <SearchBar
              setVisibleModal={this.setVisibleModal}
              selectedStudent={selectedStudent}
              receiptSelecteds={receiptSelecteds}
              handleResetWhenChange={this.resetDataToInit}
              handleAddOther={this.handleAddOther}
              handleRemoveReceipt={this.handleRemoveReceipt}
              wrappedComponentRef={formRef => {
                this.formRef = formRef;
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              rowSelection={rowSelection}
              columns={this.columns}
              dataSource={tuitionReceipts}
              rowKey="index"
              pagination={false}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Checkbox
              checked={this.state.isPrintAfterSave}
              onChange={this.togglePrintAfterSave}
              disabled={receiptSelecteds.length === 0}
            >
              In phiếu khi lưu
            </Checkbox>
            <Btn
              icon="save"
              onClick={() => this.saveReceipts()}
              disabled={receiptSelecteds.length === 0}
              loading={loadingSaveReceipt}
            >
              Lưu phiếu
            </Btn>
          </Col>
        </Row>
        <Modal
          visible={this.state.visibleModal}
          width="80%"
          style={{ minWidth: '1200px' }}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={null}
        >
          <Row>
            <Col span={24}>
              <SearchBarStudent handleParamSearchStudent={this.handleParamSearchStudent} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                columns={this.columnsStudentSearch}
                paramSearch={paramSearchStudent}
                service={getStudents}
                allowGetData={Object.keys(paramSearchStudent).length > 0}
                pagination={false}
                rowKey="studentId"
              />
            </Col>
          </Row>
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CurrentTuitionReceipts />
  </Page>
);



